<script>
import axios from 'axios';

export default {
    data() {
        return {
            courseData: [],
            //学号
            studentID: '',

        }
    },
    mounted() {
        const storedID = sessionStorage.getItem('studentID');
        this.studentID=storedID;
        this.getByID();


    },
    methods: {
        //根学号查询学生表
        getByID(){
            axios.get(`/api/course/studentId/${this.studentID}`).then((result) => {
                this.courseData=result.data.data;
            });
        },
        //查询课程表

        
    },
}
</script>

